@import "https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&family=Poppins:wght@500;600&family=Raleway:wght@500&display=swap";
@font-face {
  font-family: Basic Sans;
  font-display: swap;
  src: local(O), url("Basic-Sans-SF.0b0a830b.woff") format("woff"), url("Basic-Sans-SF.9f7f7b80.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

*, :after, :before {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  color: #212121;
  background: #fff;
  margin: 0;
  font-family: Open Sans, sans-serif;
  font-size: 18px;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button, input, textarea {
