  font-family: inherit;
  font-size: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
}

.primary-button {
  color: #fff;
  cursor: pointer;
  background-color: #37806b;
  border: none;
  border-radius: 20px 0;
  outline: none;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 700;
  line-height: 4.33;
  display: flex;
}

.secondary-button {
  color: #fff;
  cursor: pointer;
  background-color: #caa892;
  border: none;
  border-radius: 15px 0;
  outline: none;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  display: flex;
}

.header {
