.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 3;
  background-color: rgba(129, 129, 129, .5);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal {
  width: 650px;
  opacity: 1;
  background-color: #fff;
  border-radius: 80px 0;
  padding: 48px 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(1.1);
}

.modal__heading {
  text-align: center;
  margin-bottom: 16px;
}

.modal__description {
  text-align: center;
}

.modal__button {
  width: 180px;
  height: 42px;
  text-transform: uppercase;
  margin: 32px auto 0;
}

.loader {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 4;
  background-color: rgba(0, 0, 0, .9);
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.dot {
  gap: 12px;
  display: flex;
  position: absolute;
  top: 40%;
  right: 30%;
}

.dot > div {
  width: 10px;
  height: 10px;
  background-color: #f8f8f8;
  border-radius: 50%;
  transform: scale(2);
  box-shadow: 0 0 10px #37806b;
}

.dot > div:nth-child(1) {
  animation: moveDot 1s linear infinite;
}

.dot > div:nth-child(2) {
  animation: moveDot 1s linear .2s infinite;
}

.dot > div:nth-child(3) {
  animation: moveDot 1s linear .3s infinite;
}

.dot > div:nth-child(4) {
  animation: moveDot 1s linear .4s infinite;
}

.dot > div:nth-child(5) {
  animation: moveDot 1s linear .5s infinite;
}

@keyframes moveDot {
  50% {
    background: #37806b;
    transform: scale(1);
  }
}

.ball {
  background-color: rgba(255, 0, 0, 0);
  display: flex;
  position: absolute;
  bottom: 25%;
  left: 25%;
}

.ball div {
  width: 20px;
  height: 20px;
  background-color: #f8f8f8;
  border-radius: 50px;
  margin: 20px;
  animation-name: jump;
  animation-duration: .9s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.ball .a {
  animation-delay: .3s;
}

.ball .u {
  animation-delay: .6s;
}

@keyframes jump {
  0% {
    transform: translateY(50px)scale(1);
  }

  100% {
    transform: translateY(10px)scale(1.8)rotate(360deg);
  }
}

.paw {
  --ballcolor: #f2f2f2;
  --shadow: 0px 0 rgba(255, 255, 255, 0);
  --shadowcolor: rgba(255, 255, 255, 0);
  width: 10px;
  height: 10px;
  color: var(--ballcolor);
  border-radius: 50%;
  animation: shadowRolling 2s linear infinite;
  position: absolute;
  bottom: 25%;
  right: 40%;
}

@keyframes shadowRolling {
  0% {
    box-shadow: var(--shadow), var(--shadow), var(--shadow), var(--shadow);
  }

  12% {
    box-shadow: 100px 0 var(--ballcolor), var(--shadow), var(--shadow), var(--shadow);
  }

  25% {
    box-shadow: 110px 0 var(--ballcolor), 100px 0 var(--ballcolor), var(--shadow), var(--shadow);
  }

  36% {
    box-shadow: 120px 0 var(--ballcolor), 110px 0 var(--ballcolor), 100px 0 var(--ballcolor), var(--shadow);
  }

  50% {
    box-shadow: 130px 0 var(--ballcolor), 120px 0 var(--ballcolor), 110px 0 var(--ballcolor), 100px 0 var(--ballcolor);
  }

  62% {
    box-shadow: 200px 0 var(--shadowcolor), 130px 0 var(--ballcolor), 120px 0 var(--ballcolor), 110px 0 var(--ballcolor);
  }

  75% {
    box-shadow: 200px 0 var(--shadowcolor), 200px 0 var(--shadowcolor), 130px 0 var(--ballcolor), 120px 0 var(--ballcolor);
  }

  87% {
    box-shadow: 200px 0 var(--shadowcolor), 200px 0 var(--shadowcolor), 200px 0 var(--shadowcolor), 130px 0 var(--ballcolor);
  }

  100% {
    box-shadow: 200px 0 var(--shadowcolor), 200px 0 var(--shadowcolor), 200px 0 var(--shadowcolor), 200px 0 var(--shadowcolor);
  }
}

.balloon {
  width: 148px;
  height: 148px;
  background-color: #ccc;
  background-image: radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0);
  background-position: 24px, 8px, -8px, -24px;
  border-radius: 50%;
  animation: flash 1s linear infinite;
  position: absolute;
  top: 25%;
  left: 40%;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, .15);
}

.balloon:before, .balloon:after {
  content: "";
  width: 16px;
  height: 18px;
  background: #fff;
  border: 1px solid #ccc;
  border-top-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.balloon:before {
  width: 0;
  height: 64px;
  transform: translate(-50%, 18px);
}

@keyframes flash {
  0% {
    background-image: radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0);
  }

  25% {
    background-image: radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0);
  }

  50% {
    background-image: radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0);
  }

  75% {
    background-image: radial-gradient(#ddd 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0);
  }

  100% {
    background-image: radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0), radial-gradient(#fff 4px, rgba(0, 0, 0, 0) 0);
  }
}

