  background-color: #111;
  border-radius: 50px;
  margin: -250px auto;
  animation: moveHead 1.5s ease-out infinite alternate;
  position: relative;
  box-shadow: inset -4px 2px #f0dcdc;
}

.body {
  width: 140px;
  height: 120px;
  background-color: #111;
  border-radius: 50px / 25px;
  margin: 90px auto;
  animation: moveBody 1.5s ease-out infinite alternate;
  position: relative;
  box-shadow: inset -5px 2px #f0dcdc;
}

@keyframes moveHead {
  0% {
    top: 0;
  }

  50% {
    top: 10px;
  }

  100% {
    top: 0;
  }
}

@keyframes moveBody {
  0% {
    top: -5px;
  }

  50% {
    top: 10px;
  }

  100% {
    top: -5px;
  }
}

@keyframes moveLabel {
  0% {
    width: 120px;
  }

  16% {
    width: 128px;
  }

  32% {
    width: 136px;
  }

  48% {
    width: 144px;
  }

  64% {
    width: 152px;
  }

  80% {
    width: 160px;
  }

  100% {
    width: 168px;
  }
}

